.CTN_ctn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.CTN_ctn2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.CTN_title {
  color: #3af6ff;
  font-size: 55px;
  text-align: center;
}
.ctn_button {
  font-weight: bold;
  color: white;
  font-size: 22px;
  width: 250px;
  height: 90px;
  background: linear-gradient(
    rgba(58, 246, 255, 0.2) 30%,
    rgba(185, 4, 139, 0.2) 60%
  );
  border-radius: 50px;
  text-decoration: none;
  border: none;
  cursor: pointer;
  margin: 0px 0 20px 0;
}

.ctn_p {
  color: white;
  font-weight: bold;
  font-size: 34px;
  margin-bottom: 90px;
  text-align: center;
}
@media (max-width: 1330px) {
  .CTN_ctn2 {
    margin-bottom: 80px;
  }
}

@media (max-width: 1024px) {
  .ctn_button {
    width: 220px;
    height: 70px;
  }
  .CTN_title {
    font-size: 40px;
  }

  .CTN_ctn p {
    font-size: 28px;
  }
}
@media (max-width: 1330px) {
  .CTN_ctn2 {
    /*  margin-bottom: 180px*/
  }
}
@media (max-width: 860px) {
  .CTN_ctn2 {
    margin-bottom: 230px;
  }
}
@media (max-width: 767px) {
  .CTN_title {
    font-size: 34px;
    margin-bottom: 80px;
  }

  .CTN_ctn p {
    font-size: 24px;
    margin-top: 80px;
  }
  .CTN_ctn2 {
    margin-top: 50px;
    margin-bottom: 0px;
  }
}
@media (max-width: 513px) {
  .CTN_title {
    font-size: 28px;
  }
  .CTN_ctn p {
    font-size: 22px;
  }
}
@media (max-width: 460px) {
  .ctn_button {
    width: 200px;
    height: 60px;
    font-size: 20px;
  }
  .CTN_title {
    font-size: 26px;
  }
  .ctn_p {
    font-size: 20px;
    margin: 0 15% 0% 7%;
  }
}
@media (max-width: 375px) {
  .CTN_title {
    margin: 0 0% 80px 0%;
  }
  .CTN_ctn p {
    font-size: 18px;
  }
  .ctn_p {
    margin: -15px 5% 7% 7%;
  }
  .CTN_ctn2 {
    margin-top: 20px;
  }
}
